.wrapper, .row {
  *zoom: 1; }
  .wrapper:before,
  .row:before,
  .wrapper:after, .row:after {
    content: " ";
    display: table; }
  .wrapper:after, .row:after {
    clear: both; }

*, *:after, *:before {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

img {
  width: auto;
  max-width: 100%;
  height: auto !important; }

.wrapper {
  width: 100%;
  margin: 0 auto;
  margin-top: 5%; }

[class*='column-'] {
  float: left;
  width: 100%;
  min-height: 1px; }

@media only screen and (min-width: 50em) {
  .wrapper {
    width: 95%;
    max-width: 65em; }
  .column-1 {
    width: 8.33333%; }
  .column-2 {
    width: 16.66667%; }
  .column-3 {
    width: 25%; }
  .column-4 {
    width: 33.33333%; }
  .column-5 {
    width: 41.66667%; }
  .column-6 {
    width: 50%; }
  .column-7 {
    width: 58.33333%; }
  .column-8 {
    width: 66.66667%; }
  .column-9 {
    width: 75%; }
  .column-10 {
    width: 83.33333%; }
  .column-11 {
    width: 91.66667%; }
  .column-12 {
    width: 100%; } }

body {
  font: 100% Helvetica, sans-serif;
  color: #fff; }

h1 {
  font-size: 2.5em; }

p {
  color: #000000; }

.main-block {
  margin-left: 10%;
  padding: 0;
  border: 2px solid black;
  background-color: #efefef;
  min-height: 365px; }

.panel-form.ng-invalid .jpinput::-webkit-input-placeholder {
  color: #e51c23; }

.panel-form.ng-invalid .jpinput:-moz-placeholder {
  color: #e51c23; }

.panel-form.ng-invalid .jpinput::-moz-placeholder {
  color: #e51c23; }

.panel-form.ng-invalid .jpinput:-ms-input-placeholder {
  color: #e51c23; }

.btn {
  border: none;
  padding: 7px 10px;
  width: 100%;
  max-width: 70px;
  color: #fff; }

.btn:hover {
  cursor: pointer; }

.add-btn {
  float: right;
  background-color: #259b24; }

.del-btn {
  background-color: #e51c23; }

.panel {
  background-color: #6889ff; }

.data table {
  margin: 0;
  padding: 0;
  height: auto;
  margin-bottom: 5%; }

.data table thead {
  color: #272ce9; }

.data table tbody {
  color: #000c35; }

.sidebar {
  background-color: #efefef;
  margin-left: 5%;
  border: 2px solid black; }

.sidebar .panel a {
  margin-top: 5%;
  display: inline-block; }

.sidebar p, .sidebar h1, .sidebar a {
  padding-left: 15px; }

.sidebar p, .sidebar a {
  font-size: 1.5em;
  margin-top: 15%;
  display: block;
  color: #000000; }

.sidebar a.small-text {
  font-size: 1em;
  text-decoration: none; }

.sidebar a.small-text:last-child {
  margin-bottom: 15%; }

.slabel {
  display: inline-block;
  max-width: 240px;
  position: relative;
  width: 100%; }

.slabel input {
  appearance: none;
  border: 0;
  border-radius: -1;
  box-sizing: border-box;
  display: inline-block;
  outline: 0;
  padding: 7px 15px;
  transition: transform 225ms ease-in-out;
  width: 100%;
  background: #efefef; }

a {
  color: #000000;
  text-decoration: none; }

a:focus {
  color: #e51c23; }

a.disabled {
  color: #AAAAAA;
  cursor: default;
  pointer-events: none;
  text-decoration: none; }
